.widget {
  display: flex;
  justify-content: space-between;
  align-items: center;
  flex: 1;
  padding: 10px;
  -webkit-box-shadow: 2px 4px 10px 1px rgba(0, 0, 0, 0.47);
  box-shadow: 2px 4px 10px 1px var(--box-shadow);
  border-radius: 10px;
  height: 100px;
}

.left,
.right {
  display: flex;
  flex-direction: column;
  justify-content: space-between;
}

.title {
  font-weight: bold;
  font-size: 14px;
  color: var(--color);
}

.counter {
  font-size: 28px;
  font-weight: 300;
  color: var(--color);
}

.link {
  width: max-content;
  font-size: 12px;
  border-bottom: 1px solid var(--border-color);
}

.percentage {
  display: flex;
  align-items: center;
  font-size: 14px;
}

.icon {
  font-size: 60px;
  padding: 5px;
  border-radius: 5px;
  align-self: flex-end;
}